import React, { useState } from 'react';
import guid from '../../utils/guid';
import { BlipLoading } from '../BlipLoading';
import { NotificationContainer } from 'react-notifications';
import { showToast } from '../../api/commomService';

import handleGetOrigin from '../../services/handleGetOrigin';
import handleSetPublished from '../../services/handleSetPublished';
import handleSetWorkingConfiguration from '../../services/handleSetWorkingConfiguration';
import handleGetWorkFlow from '../../services/handleGetWorkFlow';

import handleGetGlobaisPublished from '../../services/handleGetGlobaisPublished';
import handleSetPublishedFlow from '../../services/handleSetPublishedFlow';
import handleGetWorkFlowPublished from '../../services/handleGetWorkFlowPublished';


function PluginDesk({ info, builder }) {

  const resultBuilder = atob(builder).split(':')[1];

  const [token, setToken] = useState('');
  const [loading, setLoading] = useState(false);
  const [keyOrigin, setKeyOrigin] = useState(resultBuilder);


  async function start(event) {

    event.preventDefault();
    setLoading(true);

    if (!token) {

      showMessage('O campo Token precisa ser preenchido', 'warning', 'Error!');
      return false;
    }

 

    const dataResource = {
        token,
        baseUrlTiny: 'https://api.tiny.com.br/api2'

    }

    const comeBackHandleGerOrigin = await handleGetOrigin(guid(), keyOrigin);
    const { data } = comeBackHandleGerOrigin;


    await handleSetPublished(guid(), data.resource, info);
    await handleSetWorkingConfiguration(guid(), dataResource, info);

    const comeBackHandleGetWorkFlow = await handleGetWorkFlow(guid(), keyOrigin);

    await handleSetPublishedFlow(comeBackHandleGetWorkFlow.data.resource, info);
    await handleGetWorkFlowPublished(guid(), comeBackHandleGetWorkFlow.data.resource, keyOrigin);


    const comeBckHandleGetGlobaisPublished = await
    handleGetGlobaisPublished(guid(), comeBackHandleGetWorkFlow.data.resource, info);
    

    if (comeBckHandleGetGlobaisPublished.status === 200) {

   

      showMessage('Suas configurações foram salvas com sucesso!', 'success', 'Done!')
      setLoading(false);
      setToken('');
    } else {
      showMessage('Erro ao salvar suas configurações', 'danger', 'Error!')
    }
  }


  function showMessage(msg, type) {

    setTimeout(() => {
      showToast({ type: type, message: msg })
      setLoading(false);
    }, 900);

  }


  const handleTokenState = (e) => {
    const { value } = e.target;
    setToken(value);
  }

 

  return (
    <div className="container w-100">
      <h1>Configuração</h1>
      <hr />
      <br></br>
      <div className="col-11">
        <label>bot key: </label>
        <input
          name="key"
          type="text"
          className="bp-input-wrapper blip-input w-100"
          placeholder="Key do bot"
          value={info}
          required disabled={true}
        />
      </div>
      <br></br><br></br>
      <div className="">
        <div className="row">
          <div className="col-6">
            <label>1 - Token:</label><br />
            <input
              type="text"
              className="bp-input-wrapper blip-input w-100"
              name="estadoDestino"
              placeholder="Digite seu Token"
              value={token}
              onChange={e => handleTokenState(e)}
            />
          </div>
        
        </div><br /><br />
      </div>
      <div className="mt-5 col-12">
        <button onClick={start} className="bp-btn bp-btn--bot bp-btn--small">
          {loading && (
            <BlipLoading />
          )}
          {loading && <span>Criando Estrutura ...</span>}
          {!loading && <span>Criar Estrutura</span>}
        </button>
      </div>
      <NotificationContainer />
    </div>
  );

}


export default PluginDesk;